@import "./styleguide.scss";

#ParticleCanvas {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #f0f0f0;

  &.active {
    background-color: #fff;
    transition: all 0.5s;
  }

  canvas {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .nameBox {
    @include posCenter;
    display: flex;
    align-items: center;
    justify-content: center;
    stroke-width: 30px;
    fill: #111;
  }
}
